<template>
	<div id="trainOrder">
		<Load v-if="loading"/>
		<div>
			<div class="top">
				<div class="query">
					<div class="querys">
						<p>
							<span>订单号</span>
							<el-input v-model="query.orderId" size="mini"></el-input>
						</p>
						<p>
							<span>乘车人</span>
							<el-input disabled size="mini"></el-input>
						</p>
						<p>
							<span>预定日期</span>
							<el-date-picker
						      type="date"
						      v-model="query.beginDate"
						      size="mini">
						    </el-date-picker>
						</p>
						<p>
							<span>至</span>
							<el-date-picker
						      type="date"
						      v-model="query.endDate"
						      size="mini">
						    </el-date-picker>
						</p>
					</div>
					<div>
						<el-button @click="search" class="search" type="primary">查询</el-button>
					</div>
				</div>
			</div>
			<div class="middle">
				<p class="tag">状态：</p>
				<div class="status">
					<p :class="{'active':stat.key==active}" @click="chooseStatus(stat.key)" v-for="stat in status">{{stat.name}}</p>
				</div>	
			</div>
			<div class="bottom">
				<table>
					<tr>
						<th>出发-到达</th>
						<th>乘车人</th>
						<th>出发时间</th>
						<th>金额</th>
						<th>状态</th>
						<th>操作</th>
					</tr>
					<tbody v-for="order in data">
						<tr class="title">
							<td colspan="6">
								<div class="left">
									<span>订单号：</span>
									<span class="orderId" @click="detail(order.orderId)">{{order.orderId}}</span>
									<span>外部订单号：</span>
									<span class="orderId">{{order.supplierOrderId}}</span>
								</div>
								<div class="right">
									<span>预定日期：</span>
									<span>{{order.createTime}}</span>
								</div>
							</td>						
						</tr>
						<tr>
							<td class="city">{{order.fromStation}}-{{order.arriveStation}}</td>
							<td>字段待定</td>
							<td>{{order.travelTime}} {{order.fromTime}}</td>
							<td class="count">{{order.payMoney}}</td>
							<td>{{order.status}}</td>
							<td class="handle">
								<el-button class="endorse">改签</el-button>
								<el-button class="cancel">退票</el-button>
							</td>
						</tr>
					</tbody>
				</table>				
			</div>
			<div class="pagination">
				<el-pagination
				    layout="prev, pager, next"
				    @current-change='pageChange'
				    :page-size='query.pageSize'
				    :total="total">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import main from './main';
	export default {
		...main
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import "../../../style/mixin.scss";
	@import "./style.scss";
</style>


